* {
  margin: 0;
  padding: 0;
}

.container {
  background: linear-gradient(to right bottom, #62cd94, #da38c4);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-wrapper {
  background-color: #12343b;
  min-width: 450px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}
.header h1 {
  font-size: 40px;
  color: #e2c72e;
  text-align: center;
  margin: 20px 0;
  letter-spacing: 5px;
}
.task-input {
  outline: none;
  width: 500px;
  padding: 15px;
  margin-right: 25px;
  font-size: 17px;
  color: blue;
  background-color: #f3eaea;
  border: 3px solid #e17918;
  border-radius: 10px;
}
.button-add {
  width: 80px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #de7614;
  cursor: pointer;
}
.list-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid #86e343;
  border-radius: 1px;
  padding: 10px;
  max-height: 25px;
}
.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: #ff6c6c;
  margin-right: 10px;
}

.button-edit {
  color: #e2d029;
  margin-right: 10px;
}

.button-delete {
  color: lightseagreen;
}

.complete {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ff6c6c;
  opacity: 0.6;
}

.list {
  width: 220px;
  background-color: #12343b;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 30px;
  margin-right: 235px;
}

input:focus,
button:focus {
  outline: none;
}
